.activity-head {
    font-size: xx-large !important;
    text-align: left;
  }
  
  .activity-title {
    text-align: left;
    font-size: large !important;
    margin: 0px;
    padding: 10px 0px;
    margin-right: 4px;
    margin-left: 15px;
    font-weight: 600;
  }

  .activity-title::first-letter{
    text-transform: uppercase;
  }
  .time-stamp {
    margin: 0px;
    padding: 5px 0px;
    margin-left: 15px;
    text-align: left;
    font-size: x-small;
    font-weight: 300;
    color: rgba(0, 0, 0, 0.54);
  }
  .activity-status {
    font-weight: 300;
    font-size: x-small;
    text-align: right;
    padding: 5px 0px;
    margin-right: 15px;
  }
  
  
  .activity-container {
    height: 100%;
      width: 100%;
    flex-direction: row;
    /* border-left: 4px solid #00b627; */
    border-radius: 4px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  }
  
  .green {
    border-left: 4px solid #00b627;
  }
  
  .red {
    border-left: 4px solid #d63535;
  }
  
  .yellow {
    border-left: 4px solid #fce300;
  }
  
  @media only screen and (max-width: 600px) {
    .activity-head {
      font-size: x-large !important;
    }
    .activity-title {
      font-size: medium !important;
    }
  }
  